import React, { Component } from 'react'
import {arrayOfTexts, choose, chooseNew} from './StringText'
import './Typing.css';

const startText = choose(arrayOfTexts);

class Typing extends Component {
  constructor(props) {
    super(props);
    this.state = {
      fullText: startText,
      text: startText.split(""),
      currentWord: startText.split(" ")[0],
      currentWrittenWord: ""
    }
    this.handleChange = this.handleChange.bind(this);
    this.handleClickNew = this.handleClickNew.bind(this);
    this.handleClickAgain = this.handleClickAgain.bind(this);
  }

  
  handleChange(evt) {
    const lastValue = evt.target.value[evt.target.value.length - 1];
    if (lastValue === " " && lastValue === this.state.text[0] && this.state.currentWrittenWord == this.state.currentWord) {
      evt.target.value = ""
      this.setState(prevState => {
        return {
          text: [...prevState.text.slice(1)],
          currentWord: prevState.text.join("").slice(1).split(" ")[0]
        }
      })
    } 
    
    if (lastValue === this.state.text[0] && lastValue != " " && evt.target.value == this.state.currentWord.slice(0, this.state.currentWrittenWord.length + 1)) {
      this.setState(prevState => {
        return {text: [...prevState.text.slice(1)]}
      })
    }

    this.setState({currentWrittenWord: evt.target.value}) 
  }

  handleClickAgain(evt) {
    this.setState(
      {
         text: this.state.fullText.split(""),
         currentWord: this.state.fullText.split(" ")[0],
         currentWrittenWord: "" 
      }
    )
  }

  handleClickNew(evt) {
    const newText = chooseNew(arrayOfTexts, this.state.fullText)
    this.setState(
      { 
        fullText: newText, 
        text: newText.split(""), 
        currentWord: newText.split(" ")[0],
        currentWrittenWord: ""
      }
    )
  }

  render() {
    let displayText = this.state.text.join("");
    if (displayText.length >= 40) {
      displayText = this.state.text.join("").slice(0, 40) + '...'
    } 
    let separator = {
      padding: 10
    }
    return (
      <div className="Typing">
        <h1>Norsk Touch Typing</h1>
        <p>Norsk Touch Typing er en gratis og reklamefri applikasjon der du kan trene på å skrive touch. Vi har hentet sitatene fra folkehelseinstituttet sin informasjon angående corona-situasjonen, slik at man kan bli eksta godt satt inn i dette.</p>

        <div className="DisplayedText">
          {
            displayText.length != 0 ?
            <h3>|{displayText}</h3> :
            <div>
            <h2>Gratulerer!</h2>
            <h3>Vil du prøve igjen?</h3>
            </div>
          }
        </div>
        <div className="InputField">
        { displayText.length != 0 &&
          <input type="text" value={this.state.currentWrittenWord} onChange={this.handleChange}/> 
        }
        </div>
        <div className="ButtonContainer">
          <button onClick={this.handleClickAgain}>Prøv Igjen</button>
          <button onClick={this.handleClickNew}>Nytt Sitat</button>
        </div>
      </div>
    )
  }
}

export default Typing;